import { motion } from "framer-motion"
import styled, { keyframes } from "styled-components"

const TitleBox = styled(motion.div)`
    margin-top: 10vh;
    transform-origin: left top;
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    padding-left: 1vw;
    justify-content: flex-start;
`
const ListTitle = styled(motion.div)`
    width: 110%;
    height: 6.3vw;
    overflow: hidden;
    display: flex;
    align-items: flex-end;
    overflow: hidden;
    position: relative; 
    position: absolute; 
    top: 3vh; 
    &:nth-child(2){
        top:calc( 3vh + 6vw );
    }
    &:nth-child(3){
        top: calc( 3vh + 11vw );
    }
`

const TextUp = keyframes`
    0%{
        transform: translateY(100%);
        opacity: 0;
    }
    100%{
        transform: translateY(0);
        opacity: 1;
    }

` 
const T = styled.span`
    font-size: 6.2vw;
    font-weight: 800;
    letter-spacing: -0.04em;
    font-family: 'Montserrat', sans-serif;
    transform: translateY(100%);
    opacity: 0;
    animation: ${TextUp} .24s ease-out ;
    animation-fill-mode: forwards;
    &:nth-child(1){
        animation-delay: 1.4s;
    }
    &:nth-child(2){
        animation-delay: 1.45s;
    }
    &:nth-child(3){
        animation-delay: 1.5s;
    }
    &:nth-child(4){
        animation-delay: 1.55s;
    }
    &:nth-child(5){
        animation-delay: 1.6s;
    }
 
`
const T2 = styled(T)`
    position: relative; 
    left: 2vw; 
    &:nth-child(1){
        animation-delay: 1.7s;
    }
    &:nth-child(2){
        animation-delay: 1.75s;
    }
    &:nth-child(3){
        animation-delay: 1.8s;
    }
    &:nth-child(4){
        animation-delay: 1.85s;
    }
    &:nth-child(5){
        animation-delay: 1.9s;
    }
    &:nth-child(6){
        animation-delay: 1.95s;
    }
    &:nth-child(7){
        animation-delay: 2s;
    }
    &:nth-child(8){
        animation-delay: 2.05s;
    }
    &:nth-child(9){
        animation-delay: 2.1s;
    }
`
const S = styled(T)`
    min-width: 1.2vw;
    font-size: 4.8vw;
    animation: ${TextUp} .22s ease-in ;
    animation-fill-mode: forwards;
    &:nth-child(1){
        animation-delay: 2.2s;
    }
    &:nth-child(2){
        animation-delay: 2.25s;
    }
    &:nth-child(3){
        animation-delay: 2.3s;
    }
    &:nth-child(4){
        animation-delay: 2.35s;
    }
    &:nth-child(5){
        animation-delay: 2.4s;
    }
    &:nth-child(6){
        animation-delay: 2.45s;
    }
    &:nth-child(7){
        animation-delay: 2.5s;
    }
    &:nth-child(8){
        animation-delay: 2.55s;
    }
    &:nth-child(9){
        animation-delay: 2.6s;
    }
`


function MainTitle () {
    return(
        <TitleBox
                initial={{ width: 0, scaleX:0}}
                animate={{ width: '40vw', scaleX: 1}}
                transition={{
                  type: "tween",
                  duration: .6,
                  delay: 1
                }}
        >
            <ListTitle>
                <T>W</T>
                <T>O</T>
                <T>R</T>
                <T>K</T>
            </ListTitle>
            <ListTitle>
                <T2>P</T2>
                <T2>O</T2>
                <T2>R</T2>
                <T2>T</T2>
                <T2>F</T2>
                <T2>O</T2>
                <T2>L</T2>
                <T2>I</T2>
                <T2>O</T2>
            </ListTitle>
            <ListTitle>
                <S>B</S>
                <S>Y</S>
                <S></S>
                <S>S</S>
                <S>I</S>
                <S>E</S>
                <S>N</S>
                <S>N</S>
                <S>A</S>
            </ListTitle>
        </TitleBox>
    )
}
export default MainTitle